import React from 'react';
import { useFirestore, useFirestoreCollection } from 'reactfire';
import 'firebase/firestore';

const Asociados = () => {
    const asociadosRef = useFirestore().collection('asociados');
    const { data, status } = useFirestoreCollection(asociadosRef);

    return (
        <>
            <h1>Asociados:</h1>
            <ul>
                {
                    status === "loading" && <h1>Loading</h1>
                }
                {
                    data?.docs?.map(asociado => (
                        <li key={asociado.id}>
                            <span>{asociado.data().documento}</span>
                            <p>{asociado.data().nombres} {asociado.data().apellidos}</p>
                        </li>
                    ))
                }
            </ul>
        </>
    )
}

export default Asociados;