import React, { useState } from 'react';

// Firebase
import 'firebase/auth';
import { useFirebaseApp, useFirestore } from 'reactfire';

const Registrar = (props) => {
    const { nombres, apellidos, documento, NO_ID_FIELD } = props.asociado;
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const asociadoRef = useFirestore().collection('asociados');

    console.log(props);

    const firebase = useFirebaseApp();

    const handleSubmit = async (_event) => {
        _event.preventDefault();

        try {
            await firebase.auth().createUserWithEmailAndPassword(email, password)
            asociadoRef.doc(NO_ID_FIELD).update({
                usuario: firebase.auth().currentUser.uid
            });
        } catch (error) {
            console.log("Error");
        }
    }

    return(
        <>
            <h3>Bienvenido:</h3>
            <span>{ nombres } { apellidos }</span><br/>
            <span>C.C { documento }</span>

            <p>Por favor ingrese los siguiente datos para completar su registro:</p>
            <form onSubmit={handleSubmit}>
                <label htmlFor="email">Correo electrónico:</label>
                <input type="email" id="email" onChange={(e) => setEmail(e.target.value)} />

                <label htmlFor="password">Contraseña:</label>
                <input type="password" id="password" onChange={(e) => setPassword(e.target.value)} />

                <button type="submit">Ingresar</button>
            </form>
        </>
    )
}

export default Registrar;