import React, { useState } from 'react';
import { Redirect, Link } from 'react-router-dom';

// Firebase
import 'firebase/auth';
import { useFirebaseApp, useUser } from 'reactfire';

const Ingresar = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const firebase = useFirebaseApp();
    const user = useUser().data;

    const handleSubmit = async (e) => {
        e.preventDefault();

        await firebase.auth().signInWithEmailAndPassword(email, password).then(result => {
            if(!result.user.emailVerified) {
                console.log("Email no verificado");

                firebase.auth().currentUser.sendEmailVerification();
                firebase.auth().signOut();
            }else{
                console.log("Ha iniciado Sesión Exitosamente")
            }
        }).catch(error => {
            console.error(error);
        });
    }

    if (user) {
        return <Redirect to="/perfil" />
    }

    return (
            <div>
                <h1>Ingresar</h1>
                <form onSubmit={handleSubmit}>
                    <label htmlFor="email">Correo electrónico:</label>
                    <input type="email" id="email" onChange={(e) => setEmail(e.target.value)} />

                    <label htmlFor="password">Contraseña:</label>
                    <input type="password" id="password" onChange={(e) => setPassword(e.target.value)} />

                    <button type="submit">Ingresar</button>
                </form>

                <Link to="/restaurar-contrasena" className="nav__menu_link">
                    Olvide mi contraseña
                </Link>

                <Link to="/registrarse" className="nav__menu_link">
                    Registrarme
                </Link>
            </div>
    )
}
export default Ingresar;