import React from 'react';
import { useFirestore, useFirestoreCollectionData } from 'reactfire';
import 'firebase/firestore';

// Components
import Registrar from './Registrar';

const Identificacion = (props) => {
    const asociadosRef = useFirestore().collection('asociados').where('documento', '==', props.documento).limit(1);
    const { data, status } = useFirestoreCollectionData(asociadosRef);

    const handleBack = () => {
        props.setDocumento(null);
    }

    return(
        <div>
            { (status === "loading") ? (<h1>Cargando...</h1>) : <button onClick={handleBack}>Volver</button>}

            { (data && data.length === 0) && <h1>No eres asociado no puedes registrarte en el sistema</h1>}

            { (data && data.length > 0 && !data[0].estado) && <h1>Eres un asociado inactivo contactanos para poder ayudarte.</h1>}

            { (data && data.length > 0 && data[0].usuario !== null && data[0].estado) && <h1>Ya tienes una cuenta registrada...</h1>}

            { (data && data.length > 0 && data[0].usuario === null && data[0].estado) && <Registrar asociado={data[0]}/>}
        </div>
    )
}

export default Identificacion;