import React, { useState } from 'react';
import { useUser } from 'reactfire';
import { Redirect } from 'react-router-dom';
import 'firebase/firestore';

// Components
import Identificacion from '../components/Identificacion';

const Registrarse = () => {
    const [ documento, setDocumento ] = useState(null);
    const user = useUser().data;

    const handleSubmit = async (_event) => {
        _event.preventDefault();

        const { identificacion } = _event.target.elements;

        if(parseInt(identificacion.value)){
            setDocumento(parseInt(identificacion.value));
        }
    }

    if (user) {
        return <Redirect to="/perfil" />
    }

    return (
        <>
            <h1>Registrarse</h1>

            { !documento && (
                <form onSubmit={handleSubmit}>
                    <label htmlFor="identificacion">Cedula de Ciudadania:</label>
                    <input type="number" id="identificacion" min="1000000" max="9999999999" placeholder="Número de Identificación" />

                    <button type="submit">Continuar</button>
                </form>
            )}

            { documento && <Identificacion documento={documento} setDocumento={setDocumento}/>}
        </>
    )
}
export default Registrarse;